import React, { useState, useEffect } from 'react';
import axios from 'axios';
import close from '../../Assets/images/close.png';

function Design() {
	const [designs, setDesigns] = useState([]);

	const fetchData = () => {
		return axios
			.get('https://res.cloudinary.com/dwyosqxlr/image/list/design.json')
			.then((response) => {
				let data = response.data.resources;
				data = data.sort(function (a, b) {
					return -(a.created_at - b.created_at);
				});
				let url = [];

				data.forEach((element) => {
					url.push(
						'https://res.cloudinary.com/dwyosqxlr/image/upload/v1680369628/' +
							element.public_id +
							'.' +
							element.format
					);
				});

				setDesigns(url);
			});
	};

	useEffect(() => {
		fetchData();
	}, []);

	const [model, setModel] = useState(false);
	const [tmpImgSrc, setTmpImgSrc] = useState('');

	const getImg = (img) => {
		setTmpImgSrc(img);
		setModel(true);
	};

	return (
		<div className="design-gallery">
			<div className={model ? 'model imgViewer' : 'model'}>
				<img src={tmpImgSrc} alt="" />
				<img
					onClick={() => {
						setModel(false);
					}}
					src={close}
					alt=""
				/>
			</div>
			<div className="designs scale-in-hor-right-no-delay">
				{designs.map((image, index) => {
					return (
						<div className="d-pics" key={index} onClick={() => getImg(image)}>
							<img src={image} alt="" srcset="" />
						</div>
					);
				})}
			</div>
		</div>
	);
}

export default Design;
